import React, { useState } from 'react'

import Right from '../images/wall.png'
import ProximitiIMG from '../images/active_green_proximity.png'
export default function SectionStep( {setNotice, setError} ) {
    const [step, setStep] = useState(1)

    const [firstname, setFirstname] = useState('')
    const [lastname, setLastname] = useState('')
    const [email, setEmail] = useState('')

    const [country, setCountry] = useState('')
    const [city, setCity] = useState('')
    const [adress, setAdress] = useState('')

    const [number, setNumber] = useState('')
    const [whatsapp, setWhatsapp] = useState('')
    const [telegram, setTelegram] = useState('')

    function nextStep() {
        if (step === 1) {
            if (firstname && lastname && email !== '') {
                setStep(prev => prev + 1)
            } else {
                setError('Not all fields are filled in Step 1!')
                setNotice(true)
                setTimeout(() => {
                    setNotice(false)
                    setError('')
                }, 4000)
            }
        } else if (step === 2) {
            if (country && city && adress !== '') {
                setStep(prev => prev + 1)
            } else {
                setError('Not all fields are filled in Step 2!')
                setNotice(true)
                setTimeout(() => {
                    setNotice(false)
                    setError('')
                }, 4000)
            }
        } else if (step === 3) {
            if (number && whatsapp && telegram !== '') {
                // Обработка данных
            } else {
                setError('Not all fields are filled in Step 3!')
                setNotice(true)
                setTimeout(() => {
                    setNotice(false)
                    setError('')
                }, 4000)
            }
        }
    }

    return(
        <section className="step">
            <img
            className="rightGround"
            src={Right}
            />

            <img 
            className="proxomityGround"
            src={ProximitiIMG}
            />
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <div className="heading-light">
                            <h2>A couple of steps to make your store popular</h2>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12">
                        <div className="whoStep">
                            <div className={step === 1 ? "nextStep activeStep" : step === 3 || step === 2 ? "nextStep stepNone" : "nextStep"}>
                                <h5>Step 1</h5>
                            </div>

                            <div className={step === 2 ? "nextStep activeStep" : "nextStep"}>
                                <h5>Step 2</h5>
                            </div>

                            <div className={step === 3 ? "nextStep activeStep" : step === 1 ? "nextStep stepNone" : "nextStep"}>
                                <h5>Step 3</h5>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row">
                    <div className="col-12">
                        {step === 1 && (
                            <div className="aboutStep">
                                <p>
                                    Step 1 description - ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod 
                                    tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, 
                                    quis nostrud exercitation ullamco laboris nisi 
                                    ut aliquip ex ea commodo consequat. Duis aute irure
                                </p>
                            </div>
                        )}
                        {step === 2 && (
                            <div className="aboutStep">
                                <p>
                                    Step 2 description - ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod 
                                    tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, 
                                    quis nostrud exercitation ullamco laboris nisi 
                                    ut aliquip ex ea commodo consequat. Duis aute irure
                                </p>
                            </div>
                        )}
                        {step === 3 && (
                            <div className="aboutStep">
                                <p>
                                    Step 3 description - ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod 
                                    tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, 
                                    quis nostrud exercitation ullamco laboris nisi 
                                    ut aliquip ex ea commodo consequat. Duis aute irure
                                </p>
                            </div>
                        )}
                    </div>
                </div>

                <div className="row">
                    <div className="col-12">
                        {step === 1 && (
                            <div className="inputGroup">
                                <input placeholder="Enter First Name"
                                value={firstname}
                                onChange={(event) => setFirstname(event.target.value)}
                                ></input>
                                <input placeholder="Enter Last Name"
                                value={lastname}
                                onChange={(event) => setLastname(event.target.value)}
                                ></input>
                                <input placeholder="Enter Email"
                                value={email}
                                onChange={(event) => setEmail(event.target.value)}
                                ></input>
                            </div>
                        )}

                        {step === 2 && (
                            <div className="inputGroup">
                                <input placeholder="Enter Country"
                                value={country}
                                onChange={(event) => setCountry(event.target.value)}
                                ></input>
                                <input placeholder="Enter City"
                                value={city}
                                onChange={(event) => setCity(event.target.value)}
                                ></input>
                                <input placeholder="Enter Adress"
                                value={adress}
                                onChange={(event) => setAdress(event.target.value)}
                                ></input>
                            </div>
                        )}

                        {step === 3 && (
                            <div className="inputGroup">
                                <input placeholder="Enter Number"
                                value={number}
                                onChange={(event) => setNumber(event.target.value)}
                                ></input>
                                <input placeholder="Enter WhatsApp"
                                value={whatsapp}
                                onChange={(event) => setWhatsapp(event.target.value)}
                                ></input>
                                <input placeholder="Enter Telegram"
                                value={telegram}
                                onChange={(event) => setTelegram(event.target.value)}
                                ></input>
                            </div>
                        )}

                        <div className="btn">
                            <a href="#" className="btnNext"
                            onClick={(event) => {
                                event.preventDefault()
                                nextStep()
                            }}
                            >Next</a>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}