import React, { useState } from 'react'
import PromoIMG from '../images/promo.png'

export default function Header() {
    const [menu, setMenu] = useState(false)

    return(
        <section className="header">
            <div className="container">
                <div className="row">
                    <div className="col-12">
                        <a href="#" 
                        onClick={(event) => {
                            event.preventDefault()
                            menu ? setMenu(false) : setMenu(true)
                        }}
                        className={menu ? 'mobileMenu mobileMenu-active' : 'mobileMenu'}><span></span></a>
                        <nav className={menu ? 'navigation' : 'navigation navigation-disable'} >
                            <a href="#" className="nextPage">home</a>
                            <a href="#" className="active">order services</a>
                            <a href="#" className="nextPage">beacon marketing</a>
                        </nav>
                    </div>
                </div>

                <div className="row">
                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12">
                        <div className="promo">
                            <img
                            src={PromoIMG}
                            className="promo_image">
                            </img>
                        </div>
                    </div>

                    <div className="col-xl-6 col-lg-6 col-md-12 col-sm-12">
                        <div className="info">
                            <h1>
                                Discount for Services
                            </h1>
                            <h3>
                                Our advertisement services cost $10 per
                                beacon per month, and we offer wholesale
                                discounts:
                            </h3>
                            <p>
                                Free installation of the beacons is also an option, but then their
                                marketing capabilities will be used by third parties also. How to Get Connected To
                                get connected to our service or find
                                out more information, please call us at (844)-295-0900 or purchase
                                our service through our site at https://fdh.cloud/order/services
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}