import React, { useState } from 'react'

export default function Goods( {title, img, price, description, link} ) {
    const [count, setCount] = useState(1)
    return(
        <div className="col-xl-3 col-lg-3 col-md-6 col-xs-12">
            <div className="product">
                <h5>{title}</h5>
                <div className="fixedIMG">
                    <img
                    src={img}
                    ></img>
                </div>
                <span className="price">{price}</span>
                <p>{description}</p>
                <div className="counter">
                    <div className="calc">
                        <a
                        onClick={(event) => {
                            event.preventDefault()
                            if (count > 1) setCount(prev => prev - 1)
                        }}
                        >-</a>
                        <input
                        value={count}
                        onChange={(event) => setCount(event.target.value)}
                        />
                        <a
                        onClick={(event) => {
                            event.preventDefault()
                            if (count < 99) setCount(prev => prev + 1)
                        }}
                        >+</a>
                    </div>
                    <span>шт</span>
                </div>
                <a 
                className="btn"
                href={link}>Buy now</a>
                
            </div>
        </div>
    )
}

Goods.defaultProps = {
    title: 'Loading',
    img: '',
    price: 'Loading',
    description: 'Loading',
    link: '#'
}